.more-details-block {
    width: 520px;
    backdrop-filter: blur(9px) saturate(100%);
    -webkit-backdrop-filter: blur(9px) saturate(100%);
    border: 5px solid rgba(255, 255, 255, 0.1);
    padding: 50px 135px 50px 35px;

    @media screen and (max-width: 960px) {
        width: 100%;
        padding: 0px 50px 50px 35px;
    }
}

.more-details-text {
    display: block;
    margin-top: 40px;
    margin-bottom: 50px;
    font-size: 18px;
    color: #FFFFFF;

    @media screen and (max-width: 460px) {
        font-size: 14px;
    }
}

.more-details-title {
    text-transform: uppercase;
    color: #FFFFFF;
    font-size: 18px;

    @media screen and (max-width: 460px) {
        font-size: 14px;
        text-align: center;
    }
}

.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 130px;
}