.temperature-data {
    display: flex;
    justify-content: space-between;
    margin-top: 33px;
}

.more-details-temp {
    color: #FFFFFF;
    font-size: 18px;
    opacity: 30%;

    @media screen and (max-width: 460px) {
        font-size: 14px;
    }
}

.more-details-temp-degrees {
    color: #FFFFFF;
    font-size: 18px;

    @media screen and (max-width: 460px) {
        font-size: 14px;
    }
}