.main-weather-block {
    color: #FFFFFF;
    display: flex;
    justify-content: end;
    align-items: end;
    margin-bottom: 85px;
    margin-left: 115px;

    @media screen and (max-width: 960px) {
        justify-content: flex-start;
        margin-left: 45px;
        margin-top: auto;
        margin-bottom: unset;
    }
}

.main-weather {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 580px;
}

.main-weather-temp {
    font-size: 143px;

    @media screen and (max-width: 460px) {
        font-size: 64px;
    }
}

.main-weather-city {
    margin-left: 10px;
}

.weather-city {
    font-size: 60px;
    width: 310px;
    overflow: hidden;
    text-overflow: ellipsis;
    text-wrap: nowrap;

    @media screen and (max-width: 460px) {
        font-size: 30px;
    }
}

.weather-date {
    font-size: 18px;
    margin-top: -15px;

    @media screen and (max-width: 460px) {
        font-size: 10px;
        margin-top: 0;
    }
}

.main-weather-icon {
    margin-top: 40px;
    margin-left: 15px;
}

.loader {
    display: flex;
    width: 50%;
    justify-content: center;
    margin-top: auto;
    margin-bottom: 100px;
}