.search-lacations {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #FFFFFF;
    height: 50px;

    @media screen and (max-width: 460px) {
        border-bottom: 1px solid #FFFFFF;
        position: absolute;
        top: -455px;
        right: 15px;
        height: 25px;
    }
}

.search-lacations-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.search-input {
    color: #FFFFFF;
    opacity: 30%;
    font-size: 20px;

    &::placeholder {
        color: #FFFFFF;
    }

    @media screen and (max-width: 460px) {
        font-size: 12px;
    }
}

.search-icon {
    margin-right: 17px;

    @media screen and (max-width: 460px) {
        width: 14px;
    }
}

.search-locations-list {
    position: absolute;
    top: 100px;
    width: 340px;
    max-height: 217px;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 0 0 7px 7px;
    color: #000000;
    overflow: hidden;

    @media screen and (max-width: 460px) {
        top: 23px;
        width: 153px;
    }
}

.search-locations-item {
    padding-left: 10px;
    font-size: 18px;
    cursor: pointer;

    &:hover {
        background-color: rgba(156, 155, 155, 0.8);
    }

    @media screen and (max-width: 460px) {
        font-size: 14px;
    }
}